@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratregular.woff2") format("woff2"), url("../fonts/montserratregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratmedium.woff2") format("woff2"), url("../fonts/montserratmedium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratsemibold.woff2") format("woff2"), url("../fonts/montserratsemibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratbold.woff2") format("woff2"), url("../fonts/montserratbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1279px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  padding: 14px 20px;
  font-family: inherit;
  font-size: 14px;
  line-height: 150%;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.logo {
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .logo:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.logo.focus-visible:focus {
  opacity: 0.7;
}
.logo img {
  width: 70px;
  height: 38px;
}
@media (max-width: 1023px) {
  .logo img {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo img {
    width: 42px;
    height: 22px;
  }
}

.header-example {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 35px 0;
  z-index: 30;
  transition: transform 0.3s ease, background-color 0.3s ease;
}
@media (max-width: 1023px) {
  .header-example {
    padding: 28px 0;
  }
}
@media (max-width: 767px) {
  .header-example {
    padding: 20px 0;
  }
}
.header-example.is-hidden {
  transform: translateY(-100%);
}
.header-example__logo {
  display: flex;
}
@media (max-width: 767px) {
  .is-open .header-example__logo {
    display: none;
  }
}
.header-example__nav {
  justify-self: end;
}
@media (max-width: 767px) {
  .header-example__nav {
    background-color: #f9fbfd;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: start;
    left: 36px;
    opacity: 0;
    margin: 0;
    padding: 20px 30px 20px 15px;
    position: fixed;
    pointer-events: none;
    top: 0;
    transition: all 0.3s ease;
    right: 0;
    transform: translateX(100%);
    visibility: hidden;
    z-index: 20;
  }
}
.header-example__nav .logo {
  display: none;
}
@media (max-width: 767px) {
  .header-example__nav .logo {
    display: inline-flex;
    margin-bottom: 35px;
  }
}
.is-open .header-example__nav {
  opacity: 1;
  pointer-events: all;
  visibility: visible;
  transform: translateX(0);
  overflow-y: scroll;
}
.is-open .header-example__nav .header-example__item {
  opacity: 1;
  transform: translateX(0);
}
.header-example__list {
  margin: 0;
}
@media (max-width: 767px) {
  .header-example__list {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .header-example__item {
    width: 100%;
    opacity: 0;
    transition: all ease-out 0.15s;
    transform: translateX(15px);
  }
}
.header-example__link {
  transition: color 0.3s ease;
}
@media (max-width: 767px) {
  .header-example__link {
    color: #011c40;
  }
}
.header-example__burger {
  width: 24px;
  height: 18px;
  justify-self: end;
  grid-column: 2;
  align-items: start;
  background-color: transparent;
  border: none;
  color: #f9fbfd;
  display: none;
  padding: 0;
  z-index: 25;
  cursor: pointer;
}
@media (max-width: 767px) {
  .header-example__burger {
    position: relative;
    top: 2px;
    margin-left: auto;
    display: flex;
    transition: all 0.3s ease;
  }
}
@media (hover: hover) {
  .header-example__burger:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header-example__burger.focus-visible:focus {
  opacity: 0.8;
}
.header-example__burger.focus-visible:active {
  opacity: 0.5;
}
.header-example__burger .is-open .header-example__burger-open {
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease 0s;
}
.header-example__burger .is-open .header-example__burger-close {
  opacity: 1;
  pointer-events: all;
  transition: opacity 0.3s ease 0.2s;
}
.header-example__burger-open {
  width: 24px;
  height: 2px;
  background-color: #f9fbfd;
  box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd;
  transition: opacity 0.3s ease 0.1s;
}
.is-open .header-example__burger-open {
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease 0s;
}
.header-example__burger-close {
  opacity: 0;
  transition: opacity 0.3s ease;
  position: absolute;
  width: 18px;
  height: 18px;
  top: 0;
  right: -2px;
  cursor: pointer;
}
.is-open .header-example__burger-close {
  opacity: 1;
  pointer-events: all;
  transition: opacity 0.3s ease 0.2s;
}
.is-open .header-example__burger-close::before, .is-open .header-example__burger-close::after {
  content: "";
  position: absolute;
  top: 9px;
  left: 0;
  width: 18px;
  height: 2px;
  background-color: #011c40;
}
.is-open .header-example__burger-close::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.is-open .header-example__burger-close::after {
  transform: rotate(-45deg);
}

.header-example_container {
  display: grid;
  grid-template-columns: 70px auto;
  column-gap: 30px;
  align-items: center;
}
@media (max-width: 1023px) {
  .header-example_container {
    grid-template-columns: 45px auto;
    column-gap: 20px;
  }
}

